import React, { Component } from "react";
import moment from 'moment';
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Table,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { ExportToCsv } from 'export-to-csv';
import { postRequest, URLS } from "../../query";
import { getMinutes, getWithExpiry, lowerBoundIndex, minutes2TimeString } from "../../function";

const type_titles = {
  all: 'All Attendance',
  late: 'Late Attendance',
  wrong: 'Wrong Class Attendance',
  missed: 'Missed Class'
}

class ReportStudent extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      students: [],
      student: '_all_',
      belts: [],
      belt: '_all_',
      classes: [],
      sel_class: '_all_',
      report_type: 'all',
      consecutive: 1,

      filtered: [],
      logs: [],
      day_start: moment().format('YYYY-MM-DD'),
      day_end: moment().format('YYYY-MM-DD'),
      token_info: getWithExpiry('token_info'),

      gen_day_start: moment().format('YYYY-MM-DD'),
      gen_day_end: moment().format('YYYY-MM-DD'),
      gen_student_name: '',
      gen_belt: '',
      gen_class: '',
      gen_report_type: 'all',
      gen_consecutive: 1,
    }
  }

  componentDidMount() {
    Promise.all([
      postRequest(URLS.student_getall, { token: this.state.token_info }),
      postRequest(URLS.belt_getall, { token: this.state.token_info }),
      postRequest(URLS.class_getall, { token: this.state.token_info })
    ])
    .then(([res1, res2, res3]) => {
      if (res1.data.success) {
        let data = res1.data.result;
        console.log(data);
        this.setState({ students: data });
        // this.fetchStaffData(data[0]?._id);
      } else {
        console.log(res1.data.message);
      }
      if (res2.data.success) {
        this.setState({ belts: res2.data.result })
      } else {
        console.log(res2.data.message);
      }

      if (res3.data.success) {
        this.setState({ classes: res3.data.result })
      } else {
        console.log(res3.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  handleClickView = () => {
    if (this.state.day_start.localeCompare(this.state.day_end) > 0) {
      this.setState({ filtered: [] });
      return;
    }

    let query = {
      createdAt: { 
        $gte: moment(this.state.day_start).toISOString(),
        $lt: moment(this.state.day_end).add(1, 'days').toISOString()
      },
      token: this.state.token_info
    }
    let newstate = {};

    if (this.state.student !== '_all_') {
      query.student = this.state.student;
      let student = this.state.students.find(s => s._id === this.state.student);
      newstate.gen_student_name = student.firstname + ' ' + student.lastname;
    } else {
      newstate.gen_student_name = '';
    }

    if (this.state.belt !== '_all_') {
      let belt = this.state.belts.find(b => b._id === this.state.belt);
      newstate.gen_belt = belt.name;
      query.belt = this.state.belt;
    } else {
      newstate.gen_belt = '';
    }
    if (this.state.sel_class !== '_all_') {
      let sel_class = this.state.classes.find(c => c._id === this.state.sel_class);
      newstate.gen_class = sel_class.name;
      query.class_id = this.state.sel_class;
    } else {
      newstate.gen_class = '';
    }

    newstate.gen_report_type = 'all';
    if (this.state.report_type === 'late') {
      query.is_late = true;
      newstate.gen_report_type = 'late'
    } else if (this.state.report_type === 'wrong') {
      query.is_wrong = true;
      newstate.gen_report_type = 'wrong'
    } else if (this.state.report_type === 'missed') {
      query.is_missed = true;
      query.consecutive = this.state.consecutive;
      newstate.gen_report_type = 'missed'
      newstate.gen_consecutive = this.state.consecutive;
    }

    newstate.gen_day_start = this.state.day_start;
    newstate.gen_day_end = this.state.day_end;
    this.setState(newstate);

    postRequest(URLS.check_student_get, query)
    .then(res => {
      if (res.data.success) {
        this.setState({ filtered: res.data.result })
      } else {
        console.log(res.data.message);
      }
    })
  }

  handleExport = () => {
    let title = 'Student Attendance Report\n'+type_titles[this.state.gen_report_type]+' '+
      (this.state.gen_report_type === 'missed' ? `${this.state.gen_consecutive} consecutive `: '')+
      (!!this.state.gen_student_name ? `Student: ${this.state.gen_student_name} ` : '')+
      (!!this.state.gen_belt ? `Belt: ${this.state.gen_belt} ` : '')+
      (!!this.state.gen_class ? `Class: ${this.state.gen_class} ` : '')+
      `(${moment(this.state.gen_day_start).format('MM/DD/YYYY')} - ${moment(this.state.gen_day_end).format('MM/DD/YYYY')})`

    let headers = ['Student', 'Belt', 'Date', 'Class'];
    if (this.state.gen_report_type !== 'missed') headers.push('');
    const options = { 
      filename: 'Student Attendance',
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalSeparator: '.',
      showLabels: true, 
      showTitle: true,
      title: title,
      useTextFile: false,
      useBom: true,
      headers
    };

    const csvExporter = new ExportToCsv(options);
    let data = this.state.filtered.map(d => {
      let nd = {
        student: d.student.firstname + ' ' + d.student.lastname,
        belt: d.belt,
        date: moment(d.createdAt).format('MM/DD/YYYY'),
        class: d.class_name,
      };
      if (this.state.gen_report_type !== 'missed') {
        nd.other =  d.is_late && d.is_wrong ? '**Late Wrong class' : d.is_late ? '**Late' : d.is_wrong ? '**Wrong class' : ''
      }
      return nd;
    });
    csvExporter.generateCsv(data);
  }

  render()
  {
    return (
      <>
      <Container>
        <Card>
          <Card.Body>
            <Row>
              <Col xs="12" sm="6" md="8">
                <Row>
                  <Col xs={12} sm={6}>
                    <Form.Group>
                      <label>From</label>
                      <div className='full-width'>
                        <Form.Control type="date" value={this.state.day_start} required onChange={e => this.setState({ day_start: e.target.value })}>
                        </Form.Control>
                      </div>
                    </Form.Group>
                  </Col>
                  <Col xs={12} sm={6}>
                    <Form.Group>
                      <label>To</label>
                      <div className='full-width'>
                        <Form.Control type="date" value={this.state.day_end} required onChange={e => this.setState({ day_end: e.target.value })}>
                        </Form.Control>
                      </div>
                    </Form.Group>
                  </Col>
                  <Col xs="12">
                    <Form.Group>
                      <label>Student</label>
                      <Form.Control
                        as="select"
                        value={this.state.student}
                        onChange={e => this.setState({ student: e.target.value })}
                      >
                        <option value='_all_'>All</option>
                        { this.state.students.map(student => (
                          <option key={student._id} value={student._id}>{student.firstname} {student.lastname}</option>
                        ))}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                  <Col xs="12">
                    <Form.Group>
                      <label>Class</label>
                      <Form.Control
                        as="select"
                        value={this.state.sel_class}
                        onChange={e => this.setState({ sel_class: e.target.value })}
                      >
                        <option value='_all_'>All</option>
                        { this.state.classes.map(item => (
                          <option key={item._id} value={item._id}>{item.name}</option>
                        ))}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                  <Col xs="12">
                    <Form.Group>
                      <label>Belt</label>
                      <Form.Control
                        as="select"
                        value={this.state.belt}
                        onChange={e => this.setState({ belt: e.target.value })}
                      >
                        <option value='_all_'>All</option>
                        { this.state.belts.map(item => (
                          <option key={item._id} value={item._id}>{item.name}</option>
                        ))}
                      </Form.Control>
                    </Form.Group>
                  </Col>
                </Row>
              </Col>
              <Col xs="12" sm="6" md="4">
                <Row className="mt-3">
                  <Col xs="12">
                    <Form.Check 
                      type='radio'
                      label='All Attendance'
                      name='report_type'
                      checked={this.state.report_type === 'all'}
                      onChange={e => this.setState({ report_type: 'all' })}
                    />
                  </Col>
                  <Col xs="12">
                    <Form.Check 
                      type='radio'
                      label='Late to class'
                      name='report_type'
                      checked={this.state.report_type === 'late'}
                      onChange={e => this.setState({ report_type: 'late' })}
                    />
                  </Col>
                  <Col xs="12">
                    <Form.Check 
                      type='radio'
                      label='Attending wrong class'
                      name='report_type'
                      checked={this.state.report_type === 'wrong'}
                      onChange={e => this.setState({ report_type: 'wrong' })}
                    />
                  </Col>
                  <Col xs="12">
                    <Form.Check 
                      type='radio'
                      label='Missed a class'
                      name='report_type'
                      checked={this.state.report_type === 'missed'}
                      onChange={e => this.setState({ report_type: 'missed' })}
                    />
                  </Col>
                  <Col xs="12" style={{ paddingLeft: 40 }}>
                    <Form.Group>
                      <Form.Label style={{ textTransform: 'capitalize' }}>Consecutive classes missed</Form.Label>
                      <Form.Control disabled={this.state.report_type !== 'missed'} 
                      min="1" type="number"
                      value={this.state.consecutive} 
                      onChange={e => this.setState({ consecutive: e.target.value })} />
                    </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col xs="12" className='d-flex align-items-end mt-4'>
                    <Form.Group>
                      <Button className="btn-fill mr-2" variant='secondary' onClick={this.handleClickView}>View</Button>
                      <Button className="btn-fill" variant='info' onClick={this.handleExport}>Export</Button>
                    </Form.Group>
                  </Col>
                </Row>
              </Col>
              <Col xs="12">
                <div className="separator mt-2 mb-4"></div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p>Student Attendance Report 
                  <br/>{type_titles[this.state.gen_report_type]}{' '}
                  {this.state.gen_report_type === 'missed' ? `${this.state.gen_consecutive} consecutive ` : ''}
                  {!!this.state.gen_student_name ? `Student: ${this.state.gen_student_name} ` : ''}
                  {!!this.state.gen_belt ? `Belt: ${this.state.gen_belt} ` : ''}
                  {!!this.state.gen_class ? `Class: ${this.state.gen_class} ` : ''}
                  ({moment(this.state.gen_day_start).format('MM/DD/YYYY')} - {moment(this.state.gen_day_end).format('MM/DD/YYYY')})
                </p>
                <Table bordered>
                  <thead>
                    <tr>
                      <th>Student</th>
                      <th>Belt</th>
                      <th>Date</th>
                      <th>Class</th>
                      { this.state.gen_report_type !== 'missed' ?  <th></th> : null }
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.filtered.map((row, idx) => (
                        <tr key={'row_'+idx}>
                          <td>{row.student.firstname} {row.student.lastname}</td>
                          <td>{row.belt}</td>
                          <td>{moment(row.createdAt).format('MM/DD/YYYY')}</td>
                          <td>{row.class_name}</td>
                          { this.state.gen_report_type !== 'missed' ?  <td>{row.is_late && row.is_wrong ? '**Late Wrong class' : row.is_late ? '**Late' : row.is_wrong ? '**Wrong class' : ''}</td> : null }
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </Container>
      </>
    );
  }
}
export default ReportStudent;
