export function getRequest(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, params)
    .then(respJson => {
      resolve(respJson);
    })
    .catch(error => {
      reject(error);
    })
  })
}

export function postRequest(url, data, params) {
  params = params || { headers: {"Content-Type": 'application/json'}};
  return new Promise((resolve, reject) => {
    axios.post(url, data, params)
    .then(respJson => {
      resolve(respJson);
    })
    .catch(error => {
      reject(error);
    })
  })
}

export function postForm(url, data, params) {
  params = params || { headers: {"Content-Type": 'multipart/form-data'}};
  var formdata = new FormData();
  for (var key in data)
  {
    formdata.append(key, data[key]);
  }
  return new Promise((resolve, reject) => {
    axios.post(url, formdata, params)
    .then(respJson => {
      resolve(respJson);
    })
    .catch(error => {
      reject(error);
    })
  })
}