import React, { Component } from "react";
import moment from 'moment';
import { Button } from "react-bootstrap";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import { getWithExpiry } from "../../function";
import { URLS, postRequest } from "../../query";

const columns = [{
  dataField: 'name',
  text: 'Name',
}, {
  dataField: 'birthday',
  text: 'Birthday',
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return moment(row.birthday).format('MM/DD/yyyy');
  }
}, {
  dataField: 'is_active',
  text: 'Is Active?',
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return <input type='checkbox' checked={row.is_active} readOnly/>
  }
}, {
  dataField: 'is_junior',
  text: 'Junior Leader?',
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return <input type='checkbox' checked={row.is_junior} readOnly/>
  }
}, {
  dataField: '',
  text: '',
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return <a href={'/admin/staff/edit?id=' + row._id}>View Details</a>
  }
}];
const pagination = paginationFactory({
  page: 1,
  alwaysShowAllBtns: true,
  showTotal: true,
  withFirstAndLast: true,
})

class Staff extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      list: [],
      token_info: getWithExpiry('token_info'),
    }
  }

  componentDidMount()
  {
    postRequest(URLS.staff_getall, { token: this.state.token_info })
    .then(response => {
      if (response.data.success)
      {
        this.setState({ list: response.data.result });
      } else {
        console.log(response.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  onAddClick = () => {
    this.props.history.push('/admin/staff/add');
  };

  render()
  {
    return (
      <>
        <Button onClick={this.onAddClick} variant='outline-secondary' className='mb-2' style={{ float: 'right' }}>Add a staff member</Button>
        <BootstrapTable
          keyField='_id'
          data={this.state.list}
          columns={columns}
          pagination={pagination}
          bordered={false}
        />
      </>
    );
  }
}

export default Staff;
