export function getWithExpiry(key) {
	const itemStr = localStorage.getItem(key)
	// if the item doesn't exist, return null
	if (!itemStr) {
		return null
	}
	const item = JSON.parse(itemStr)
	const now = new Date()
	// compare the expiry time of the item with the current time
	if (now.getTime() > item.expiry) {
		// If the item is expired, delete the item from storage
		// and return null
		localStorage.removeItem(key)
		return null
	}
	return item.value
}

export function setWithExpiry(key, value, ttl) {
	const now = new Date()

	// `item` is an object which contains the original value
	// as well as the time when it's supposed to expire
	const item = {
		value: value,
		expiry: now.getTime() + ttl,
	}
	localStorage.setItem(key, JSON.stringify(item))
}

const letters = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ';
export function genAuthorisationNumber(length) {
  length = length || 6;
  var ret = "";

  for (var i=0; i<length; i++) {
    var r = Math.floor(Math.random()*10000) % 26;
    ret = ret + letters[r];
  }
  return ret;
}

export function getMonday(od)
{
	var d = new Date(od);
	var day = d.getDay();
	var diff = d.getDate() - day + (day == 0 ? -6:1);
	d.setDate(diff);
	d.setHours(0, 0, 0);
	return d;
}

export function addDay(od, n)
{
	var d = new Date(od);
	d.setDate(d.getDate() + n);
	return d;
}

export function getMinutes(str)
{
	var a = str.split(':');
	return a[0]*60+a[1]*1;
}

export function minutes2TimeString(mins) 
{
	return Math.floor(mins/60)+':'+('00'+(mins%60)).slice(-2);
}

export function repeatedArray(re, n)
{
	var arr = [];
	for (var i=0; i<n; i++)
		arr.push(re);
	return arr;
}

export function lowerBoundIndex(arr, key, val, comp)
{
	// assuming arr is sorted by the key in ascending order, and the value is of string
	if (arr.length === 0) return 0;

	let lo = 0, hi = arr.length;
	while (lo < hi - 1) {
		let cur = Math.floor((lo+hi)/2);
		if (comp(val, arr[cur][key]) > 0) lo = cur;
		else hi = cur;
	}

	if (comp(arr[lo][key], val) >= 0) return lo;
	return hi;
}