import React, { Component } from 'react';
import { store } from 'react-notifications-component';
import moment from 'moment';
import {
  Accordion,
  Button,
  Card,
  Container,
  Form,
  Modal,
} from "react-bootstrap";

import { URLS, postRequest, postForm } from '../../query';
import { getWithExpiry } from '../../function';
const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];

class StudentAddclass extends Component
{
  constructor(props)
  {
    super(props);
    var url = new URL(window.location.href);
    this.state = {
      _id: url.searchParams.get('id'),
      token_info: getWithExpiry('token_info'),
      classes: [],
      student: {},
      attendance: [],

      view_regular: true
    }
  }

  componentDidMount()
  {
    Promise.all([postRequest(URLS.schedule_getall, { token: this.state.token_info }),
                postRequest(URLS.student_get, { _id: this.state._id, token: this.state.token_info })])
    .then(([res1, res2]) => {
      var classes = [];
      var student = {};

      if (res1.data.success)
      {
        classes = res1.data.result;
      }
      else
      {
        this.showMessage(res1.data.message);
      }
      if (res2.data.success) {
        student = res2.data.result;
      }
      else
      {
        this.showMessage(res2.data.message);
      }
      this.setState({ classes, student, attendance: student.attendance || [] });
    }).catch(e => {
      console.log('Failed to fetch data');
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onSaveClick = () => {
    postForm(URLS.student_edit, 
    { 
      _id: this.state._id, 
      token: this.state.token_info,
      attendance: JSON.stringify(this.state.attendance)
    }).then(resp => {
      if (resp.data.success) {
        this.props.history.goBack();
      }
      else
      {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to save');
    })
  }

  onChecked = (class_id, schedule_id, checked) => {
    var attendance = Object.assign([], this.state.attendance);
    var cl_idx = attendance.findIndex(cl => cl.class_id == class_id);
    if (cl_idx < 0) {
      if (!checked) return;
      attendance.push({ class_id, schedules: []});
      cl_idx = attendance.length - 1;
    }
    var schedules = Object.assign([], attendance[cl_idx].schedules);
    if (checked && schedules.indexOf(schedule_id) == -1)
    {
      schedules.push(schedule_id);
    }
    else if (!checked && schedules.indexOf(schedule_id) != -1)
    {
      schedules.splice(schedules.indexOf(schedule_id), 1);
    }

    if (schedules.length == 0)
    {
      attendance.splice(cl_idx, 1);
    }
    else
    {
      attendance[cl_idx] = { 
        class_id,
        schedules
      }
    }
    this.setState({ attendance });
  }

  onCloseClick = () => {
    this.props.history.goBack();
  }

  onViewToggle = () => {
    this.setState({ view_regular: !this.state.view_regular });
  }

  render()
  {
    var classes = this.state.classes.filter(sc => sc.class[0] && sc.class[0].is_regular == this.state.view_regular && sc.class[0].is_active);
    return (
      <>
        <div className='horizontal-centering-container pl-3 pr-3 mb-4'>
          <div className=''>
            Select { this.state.view_regular ? 'regular/weekly' : 'special'} class schedules for {this.state.student.firstname + ' ' + this.state.student.lastname}
          </div>
          <div className='horizontal-centering-container' style={{ marginLeft: 'auto', marginRight: 0 }}>
            <Button onClick={this.onSaveClick} variant='secondary' className='btn-fill mr-2' style={{ float: 'right' }}>Save</Button>
            <Button onClick={this.onCloseClick} variant='outline-secondary' className='mr-2' style={{ float: 'right' }}>Close View</Button>
            <Button onClick={this.onViewToggle} variant='outline-secondary' style={{ float: 'right' }}>
              { this.state.view_regular ? 'View special classes' : 'View regular classes' }
            </Button>
          </div>
        </div>
        <Container fluid>
        {
          classes.length ? classes.map((cl, key) => (
            <Accordion key={cl._id}>
              <Card className='mb-0'>
                <Card.Header className='pt-0 px-0'>
                  <Accordion.Toggle as={Button} className='full-width' style={{ textAlign: 'left' }} variant="link" eventKey={cl._id}>
                    <input type='checkbox' className='mb-2 mr-2' 
                      checked={!!this.state.attendance.find(c => c.class_id == cl._id)}
                      readOnly/>
                    {cl.class[0].name}
                  </Accordion.Toggle>
                </Card.Header>
                  
                <Accordion.Collapse eventKey={cl._id}>
                  <Card.Body>
                    {
                      cl.schedules.length ? cl.schedules.map((sc, k) => {
                        if (sc.is_regular != cl.class[0].is_regular) return null;
                        var att = this.state.attendance.find(a => a.class_id == cl._id);
                        return (
                        <Form.Group className='horizontal-centering-container my-1' key={sc._id}>
                          <input type='checkbox' className='mb-2 mr-2' 
                          checked={!!att && att.schedules.indexOf(sc._id) != -1}
                          onChange={e => this.onChecked(cl._id, sc._id, e.target.checked)}/>
                          <label>
                            <span style={{ width: 120, float: 'left' }}>{ sc.is_regular ? weekdays[sc.weekday] : moment(sc.date).format('YYYY-MM-DD')}</span>
                            <span>{sc.time_start} - {sc.time_end}</span>
                          </label>
                        </Form.Group>
                        )
                      }) : 'No schedules'
                    }
                  </Card.Body>
                  
                </Accordion.Collapse>
              </Card>
            </Accordion>
          )) : <div className='ml-4'>No classes</div>
        }
        </Container>
      </>
    )
  }
}

export default StudentAddclass;