import React, { Component, createRef } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import ReactNotification from 'react-notifications-component'
import AdminNavbar from "../components/Navbars/AdminNavbar";
// import Footer from "../components/Footer/Footer";
import Sidebar from "../components/Sidebar/Sidebar";
import Login from '../views/Login';
import routes from "../routes.js";

import sidebarImage from "../assets/img/sidebar-5.jpg";
import { getWithExpiry } from "../function";

class Admin extends Component {
  constructor(props)
  {
    super(props);
    this.state = {
      logged_in: !!getWithExpiry('token_info'),
      image: sidebarImage,
      color: "black",
      hasImage: true,
    }

    this.mainPanel = createRef();
  }

  getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        var path = prop.layout + prop.path;
        if (prop.additional) path += prop.additional; 
        return (
          <Route
            path={path}
            exact={!prop.additional}
            render={(props) => <prop.component {...props} />}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  componentDidMount() {
    if (!this.state.logged_in) return;
    
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    this.mainPanel.current.scrollTop = 0;
    if (
      window.innerWidth < 993 &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
      var element = document.getElementById("bodyClick");
      element.parentNode.removeChild(element);
    }
  }

  setLoggedIn = (status) => {
    this.setState({ logged_in: status });
  }

  render() {
    let cur_path = window.location.pathname;
    let logged_in = this.state.logged_in;

    if (!logged_in && cur_path !== '/admin/login')
      return <Redirect to="/admin/login" />

    return (
      <div className="wrapper">
        <ReactNotification />
        {
          logged_in ?
          <Sidebar color={this.state.color} image={this.state.hasImage ? this.state.image : ""} routes={routes} />
          : null
        }
        <Switch>
          <Route path="/admin/login" render={(props) => <Login {...props} setLoggedIn={this.setLoggedIn} />} />
          <div className="main-panel" ref={this.mainPanel}>
            {
              logged_in ? <AdminNavbar setLoggedIn={this.setLoggedIn} /> : null
            }
            <div className="content">
              {this.getRoutes(routes)}
            </div>
            {/* <Footer /> */}
          </div>
        </Switch>
      </div>
    );
  }
}

export default Admin;
