import React, { useState, useEffect } from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import { Form, Container, Button, Row, Col } from 'react-bootstrap';
import ReactNotification from 'react-notifications-component'

import MainNavbar from "../../components/Navbars/Navbar";
import { postRequest, URLS } from '../../query';

const SelectStaff = ({ history }) => {
  const [staffList, setStaffList] = useState([]);

  useEffect(() => {
    fetchList();
  }, []);

  const goBack = () => {
    history.goBack();
  }

  const fetchList = () => {
    let loc_id = localStorage.getItem('location');
    postRequest(URLS.staff_getall, { loc_id, for: 'front' })
    .then(res => {
      if (res.data.success) {
        let list = res.data.result;
        setStaffList(list);
      }
    }, e => {
      console.log(e);
    });
  }

  return (
    <>
    <MainNavbar />
    <ReactNotification />
    <div className='content-wrapper'>
      <Container className="mt-3">
        <Row>
          <Col className="d-flex align-items-center mb-4">
            <h5>Staff Checkin</h5>
            <Button variant="outline-secondary" className="ml-auto" onClick={goBack}>Start Over</Button>
          </Col>
        </Row>
        <Row>
          {staffList.map(s => (
            <Col key={s._id} xs={12} sm={6} md={4}>
              <NavLink to={'/check/staff?id='+s._id} ><strong>{s.name}</strong></NavLink>
            </Col>
          ))}
        </Row>
      </Container>
    </div>
    </>
  )
}

export default withRouter(SelectStaff);