import React, { Component } from "react";

import {
  Button,
  Modal,
  Card,
  Form,
  Table,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import moment from 'moment';
import { store } from 'react-notifications-component';
import { postRequest, URLS } from "../../query";
import { getWithExpiry } from '../../function';

const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
class Schedule extends Component {
  constructor(props)
  {
    super(props);

    var url = new URL(window.location.href);
    this.state = {
      class_id: url.searchParams.get('id'),
      token_info: getWithExpiry('token_info'),

      class_name: '',
      class_is_regular: true,
      list: [],
      rooms: [],
      staffs: [],

      show_modal: 0,
      show_assign_modal: false,
      show_confirm_modal: false,

      session_id: '',
      session_date: moment().format('YYYY-MM-DD'),
      session_room_id: "",
      session_weekday: 1,
      session_start: "",
      session_end: "",
      session_staffs: []
    }
  }

  componentDidMount()
  {
    Promise.all([postRequest(URLS.schedule_getbyclass, { class_id: this.state.class_id, token: this.state.token_info }),
                postRequest(URLS.room_getall, { token: this.state.token_info }),
                postRequest(URLS.staff_getall, { token: this.state.token_info })])
    .then(([res1, res2, res3]) => {
      var rooms = [];
      var staffs = [];
      if (res2.data.success)
      {
        rooms = res2.data.result;
      }
      if (res3.data.success)
      {
        staffs = res3.data.result.filter(st => st.is_active);
      }
      if (res1.data.success)
      {
        var ret = res1.data.result;
        var schedules = ret.schedules;
        for (var i=0; i<schedules.length; i++)
        {
          var staff_names = [];
          for (var j=0; j<schedules[i].staffs.length; j++)
          {
            var staff = staffs.find(s => s._id == schedules[i].staffs[j]);
            if (staff) staff_names.push(staff.name);
          }
          var staff_str = "";
          if (staff_names.length > 2)
          {
            staff_str = staff_names[0] + ',' + staff_names[1] + '(+' + (staff_names.length - 2) + 'more)';
          }
          else
          {
            staff_str = staff_names.join(',');
          }
          schedules[i].staff_str = staff_str;
        }
        // console.log(ret);
        this.setState({ list: ret.schedules, class_name: ret.class.name, class_is_regular: ret.class.is_regular, rooms, staffs })
      }
      else
      {
        this.showMessage(res1.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  closeView = () => {
    this.props.history.goBack();
  }

  addNewSession = () => {
    this.setState({ 
      show_modal: 1,
      session_room_id: this.state.rooms[0]._id,
      session_date: new Date(),
      session_weekday: 1,
      session_start: "",
      session_end: "",
    });
  }

  onMofifyClicked = (schedule) => {
    this.setState({
      show_modal: 2,
      session_id: schedule._id,
      session_room_id: schedule.room_id,
      session_date: schedule.date,
      session_weekday: schedule.weekday,
      session_start: schedule.time_start,
      session_end: schedule.time_end,
    })
  }

  onAssignClicked = (schedule) => {
    this.setState({
      show_assign_modal: true,
      session_id: schedule._id,
      session_staffs: schedule.staffs,
    })
  }

  onAssignChanged = (staff_id, checked) => {
    if (checked && this.state.session_staffs.indexOf(staff_id) == -1)
    {
      var staffs = Object.assign([], this.state.session_staffs);
      staffs.push(staff_id);
      this.setState({ session_staffs: staffs });
    }
    else if (!checked && this.state.session_staffs.indexOf(staff_id) != -1)
    {
      var staffs = Object.assign([], this.state.session_staffs);
      var idx = this.state.session_staffs.indexOf(staff_id);
      staffs.splice(idx, 1);
      this.setState({ session_staffs: staffs });
    }
  }

  onDeleteClicked = (schedule) => {
    this.setState({
      session_id: schedule._id,
      show_confirm_modal: true
    })
  }

  handleOk = () => {
    var url = this.state.show_modal == 1 ? URLS.schedule_add : URLS.schedule_edit;
    var data = {
      class_id: this.state.class_id,
      is_regular: this.state.class_is_regular,
      room_id: this.state.session_room_id,
      weekday: this.state.session_weekday,
      date: this.state.session_date,
      time_start: this.state.session_start,
      time_end: this.state.session_end,
      token: this.state.token_info
    }

    if (this.state.show_modal == 2) {
      data._id = this.state.session_id;
    }
    else
    {
      data.staffs = [];
    }
    this.handleClose();
    postRequest(url, data).then(resp => {
      if (!resp.data.success)
        this.showMessage(resp.data.message);
      else
      {
        window.location.reload();
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to add session');
    })
  }

  handleAssign = () => {
    var url = URLS.schedule_edit;
    var data = {
      _id: this.state.session_id,
      staffs: this.state.session_staffs,
      token: this.state.token_info
    }

    this.handleCloseAssignModal();
    postRequest(url, data).then(resp => {
      if (!resp.data.success)
        this.showMessage(resp.data.message);
      else
      {
        window.location.reload();
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to add session');
    })
  }

  confirmedDelete = () => {
    postRequest(URLS.schedule_remove, { _id: this.state.session_id, token: this.state.token_info })
    .then(resp => {
      if (resp.data.success)
      {
        window.location.reload();
      }
      else
      {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to delete schedule');
    })
  }

  handleClose = () => {
    this.setState({ show_modal: 0 });
  }

  handleCloseConfirm = () => {
    this.setState({ show_confirm_modal: false });
  }

  handleCloseAssignModal = () => {
    this.setState({ show_assign_modal: false });
  }

  render()
  {
    return (
      <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Class Schedule for {this.state.class_name}</Card.Title>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                <Table className="table-hover table-striped">
                  <thead>
                    <tr>
                      <th className="border-0">Date</th>
                      <th className="border-0">Timeline</th>
                      <th className="border-0"># Students</th>
                      <th className="border-0">Instructors</th>
                      <th className="border-0" style={{ width: 56 }}></th>
                      <th className="border-0" style={{ width: 56 }}></th>
                      <th className="border-0" style={{ width: 56 }}></th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.list.map(sc => (
                        <tr key={sc._id}>
                          <td>{ this.state.class_is_regular ? weekdays[sc.weekday] : moment(sc.date).format('MM/DD/YYYY')}</td>
                          <td>{ sc.time_start } - { sc.time_end }</td>
                          <td>{ sc.num_students }</td>
                          <td>{ sc.staff_str }</td>
                          <td><Button variant='outline-secondary' onClick={() => this.onMofifyClicked(sc)}>Modify</Button></td>
                          <td><Button variant='outline-secondary' onClick={() => this.onAssignClicked(sc)}>Assign</Button></td>
                          <td><Button variant='danger' onClick={() => this.onDeleteClicked(sc)}>Delete</Button></td>
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
          <Row>
            <Col md="12">
              <Button onClick={this.closeView} className="float-right mr-1" variant="outline-secondary">Close this view</Button>
              <Button onClick={this.addNewSession} className="btn-fill float-right ml-auto mr-1" variant="outline-secondary">Add class session</Button>
            </Col>
          </Row>
        </Container>
        <Modal
          show={!!this.state.show_modal}
          onHide={this.handleClose}
          backdrop="static"
          keyboard={false}
        >
          <Modal.Header closeButton>
            <Modal.Title>{this.state.show_modal == 2 ? 'Modify Session' : 'Add Session' }</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group>
                <label>Class</label>
                <Form.Control
                  type="text"
                  value={this.state.class_name}
                  readOnly
                ></Form.Control>
              </Form.Group>
              <Form.Group className={ this.state.class_is_regular ? '' : 'hidden'}>
                <label>Room</label>
                <Form.Control
                  as='select'
                  value={this.state.session_room_id}
                  onChange={e => this.setState({ session_room_id: e.target.value })}
                >
                  {
                    this.state.rooms.map((room, key) => (
                      <option key={room._id} value={room._id}>{room.name}</option>
                    ))
                  }
                </Form.Control>
              </Form.Group>
              <Form.Group className={ this.state.class_is_regular ? '' : 'hidden'}>
                <label>Date</label>
                <Form.Control
                  as='select'
                  value={this.state.session_weekday}
                  onChange={e => this.setState({ session_weekday: e.target.value })}
                >
                  <option value="1">Monday</option>
                  <option value="2">Tuesday</option>
                  <option value="3">Wednesday</option>
                  <option value="4">Thursday</option>
                  <option value="5">Friday</option>
                  <option value="6">Saturday</option>
                  <option value="0">Sunday</option>
                </Form.Control>
              </Form.Group>
              <Form.Group className={ this.state.class_is_regular ? 'hidden' : '' }>
                <label>Date</label>
                <Form.Control
                  type="date"
                  value={this.state.session_date}
                  onChange={e => this.setState({ session_date: e.target.value })}
                ></Form.Control>
              </Form.Group>
              <Form.Group>
                <label>Start Time</label>
                <Form.Control
                  type="time"
                  value={this.state.session_start}
                  step="60"
                  onChange={e => this.setState({ session_start: e.target.value })}
                ></Form.Control>
              </Form.Group>
              <Form.Group>
                <label>End Time</label>
                <Form.Control
                  type="time"
                  value={this.state.session_end}
                  step="60"
                  onChange={e => this.setState({ session_end: e.target.value })}
                ></Form.Control>
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleClose}>
              Cancel
            </Button>
            <Button variant="primary" onClick={this.handleOk} className='btn-fill'>{this.state.show_modal == 2 ? 'Modify' : 'Add' }</Button>
          </Modal.Footer>
        </Modal>
        <Modal show={this.state.show_assign_modal} onHide={this.handleCloseAssignModal}>
          <Modal.Header closeButton>
            <Modal.Title>Assign instructors</Modal.Title>
          </Modal.Header>
          <Modal.Body style={{ maxHeight: '80%' }}>
            <Form>
              {
                this.state.staffs.map(staff => (
                  <Form.Group className='horizontal-centering-container' key={staff._id}>
                    <input type='checkbox' className='mb-2 mr-2' checked={this.state.session_staffs.indexOf(staff._id) != -1 }
                    onChange={e => this.onAssignChanged(staff._id, e.target.checked)} />
                    <label>{staff.name}</label>
                  </Form.Group>
                ))
              }
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseAssignModal}>
              Cancel
            </Button>
            <Button variant="danger" onClick={this.handleAssign}>
              Save
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal show={this.state.show_confirm_modal} onHide={this.handleCloseConfirm}>
          <Modal.Header closeButton>
            <Modal.Title>Warning</Modal.Title>
          </Modal.Header>
          <Modal.Body>Are you sure to delete this schedule?</Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.handleCloseConfirm}>
              Cancel
            </Button>
            <Button variant="danger" onClick={this.confirmedDelete}>
              OK
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
}

export default Schedule;
