import React, { Component } from "react";
import moment from 'moment';
import {
  Alert,
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Modal,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { postRequest, URLS } from "../../query";
import { getWithExpiry } from "../../function";

class Editstaff extends Component
{
  constructor(props)
  {
    super(props);

    var url = new URL(window.location.href);
    this.state = {
      _id: url.searchParams.get('id'),
      name: '',
      birthday: moment().format('YYYY-MM-DD'),
      is_active: false,
      is_junior: false,
      notes: '',
      token_info: getWithExpiry('token_info'),
      confirm_show: false,
    }
  }

  componentDidMount()
  {
    postRequest(URLS.staff_get, { _id: this.state._id, token: this.state.token_info })
    .then(resp => {
      if (resp.data.success)
      {
        var result = resp.data.result;
        this.setState({
          name: result.name,
          birthday: moment(result.birthday).format('YYYY-MM-DD'),
          is_active: result.is_active,
          is_junior: result.is_junior,
          notes: result.notes,
        });
      }
      else
      {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      this.showMessage('Failed to get data');
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onCloseClick = () => {
    this.props.history.goBack();
  }

  saveCurrent = (nexturl) => {
    postRequest(URLS.staff_edit, {
      _id: this.state._id,
      name: this.state.name,
      birthday: this.state.birthday,
      is_active: this.state.is_active,
      is_junior: this.state.is_junior,
      notes: this.state.notes,
      token: this.state.token_info
    }).then(resp => {
      if (resp.data.success) {
        if (nexturl) this.props.history.replace(nexturl);
        else this.props.history.goBack();
      }
      else {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to save')
    });
  }

  onSaveClick = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent();
  }

  onDeleteClick = (e) => {
    this.setState({ confirm_show: true });
  }

  handleClose = () => {
    this.setState({ confirm_show: false });
  }

  confirmedDelete = () => {
    postRequest(URLS.staff_remove, { _id: this.state._id, token: this.state.token_info })
    .then(resp => {
      if (resp.data.success)
      {
        this.props.history.goBack();
      }
      else 
      {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage("Failed to remove staff");
    })
  }

  onShowSchedule = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent('/admin/staff/schedule?id='+this.state._id);
  }

  render()
  {
    return (
      <>
      <Container>
        <Form id='form'>
          <Row>
            <Col>
              <Card>
                <Card.Header>
                  <Card.Title as="h4">Add a staff member</Card.Title>
                </Card.Header>
                <Card.Body>
                  <Row>
                    <Col>
                      <Form.Group>
                        <label>Name</label>
                        <Form.Control
                          placeholder="Company"
                          type="text"
                          required
                          value={this.state.name}
                          onChange={e => this.setState({ name: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Form.Group>
                        <label>Birthday</label>
                        <div className='full-width'>
                          <Form.Control type="date" value={this.state.birthday} required onChange={e => this.setState({ birthday: e.target.value })}>
                          </Form.Control>
                        </div>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container mt-4'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_active} onChange={e => this.setState({ is_active: e.target.checked })} />
                        <label>Is this staff member active?</label>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_junior} onChange={e => this.setState({ is_junior: e.target.checked })} />
                        <label>Is this a Junior Leader?</label>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12" className="mt-2">
                      <Form.Group>
                        <label>Notes</label>
                        <Form.Control
                          cols="80"
                          placeholder="Please add notes..."
                          rows="4"
                          as="textarea"
                          value={this.state.notes}
                          onChange={e => this.setState({ notes: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Button onClick={this.onSaveClick} className="btn-fill float-right ml-auto mr-1" type="submit" variant="info" id='btn-submit'>Save</Button>
            {/* <Button onClick={this.onNotesClick} className="float-right mr-1" variant="light">Notes</Button> */}
            <Button onClick={this.onShowSchedule} className="float-right mr-1" variant="outline-secondary" type='submit'>Show Schedule</Button>
            <Button onClick={this.onDeleteClick} className="float-right mr-1" variant="danger">Delete</Button>
            <Button onClick={this.onCloseClick} className="float-right" variant="outline-secondary">Close this view</Button>
          </Row>
        </Form>
      </Container>
      <Modal show={this.state.confirm_show} onHide={this.handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Warning</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure to remove this staff?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={this.handleClose}>
            Cancel
          </Button>
          <Button variant="danger" onClick={this.confirmedDelete}>
            OK
          </Button>
        </Modal.Footer>
      </Modal>
      </>
    );
  }
}
export default Editstaff;
