import React, { Component } from "react";
import moment from 'moment';
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Table,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { ExportToCsv } from 'export-to-csv';
import { postRequest, URLS } from "../../query";
import { getWithExpiry, lowerBoundIndex } from "../../function";

class ReportContract extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      students: [],
      filtered: [],
      day_start: moment().format('YYYY-MM-DD'),
      day_end: moment().format('YYYY-MM-DD'),
      token_info: getWithExpiry('token_info')
    }
  }

  componentDidMount() {
    postRequest(URLS.student_getall, { token: this.state.token_info })
    .then(res => {
      if (res.data.success) {
        let data = res.data.result;
        this.setState({ students: data.sort((a, b)  => a.date_end.localeCompare(b.date_end))});
      } else {
        console.log(res.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  handleClickView = () => {
    if (this.state.day_start.localeCompare(this.state.day_end) > 0) {
      this.setState({ filtered: [] });
      return;
    }

    let date_start = moment(this.state.day_start).toISOString();
    let date_end = moment(this.state.day_end).add(1, 'days').toISOString();
    const comp_func = (a, b) => a.localeCompare(b)

    let lo = lowerBoundIndex(this.state.students, 'date_end', date_start, comp_func);
    let hi = lowerBoundIndex(this.state.students, 'date_end', date_end, comp_func);
    let year = moment(this.state.day_start).year();

    this.setState({ filtered: this.state.students.slice(lo, hi)});
  }

  handleExport = () => {
    const options = { 
      filename: 'Contract Report',
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalSeparator: '.',
      showLabels: true, 
      showTitle: true,
      title: 'Contract Report (' + moment(this.state.day_start).format('MM/DD/YYYY') + ' - ' + moment(this.state.day_end).format('MM/DD/YYYY') + ')',
      useTextFile: false,
      useBom: true,
      headers: ['Name', 'Belt', 'Expires']
    };

    const csvExporter = new ExportToCsv(options);
    csvExporter.generateCsv(this.state.filtered.map(s => ({ 
      name: s.firstname + ' ' + s.lastname,
      belts: s.belts,
      date_end: moment(s.date_end).format('MM/DD/YYYY'),
    })));
  }

  render()
  {
    return (
      <>
      <Container>
        <Card>
          <Card.Body>
            <Row>
              <Col xs={12} sm={4} md={3}>
                <Form.Group>
                  <label>From</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_start} required onChange={e => this.setState({ day_start: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs={12} sm={4} md={3}>
                <Form.Group>
                  <label>To</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_end} required onChange={e => this.setState({ day_end: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs={12} sm="4" md="6" className='d-flex align-items-end'>
                <Form.Group>
                  <Button className="btn-fill mr-2" variant='secondary' onClick={this.handleClickView}>View</Button>
                  <Button className="btn-fill" variant='info' onClick={this.handleExport}>Export</Button>
                </Form.Group>
              </Col>
              <Col>
                <div className="separator mt-2 mb-4"></div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p>Contract Report ({moment(this.state.day_start).format('MM/DD/YYYY')} - {moment(this.state.day_end).format('MM/DD/YYYY')})</p>
                <Table bordered>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Belt</th>
                      <th>Expires</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.filtered.map((row, idx) => (
                        <tr key={'row_'+idx}>
                          <td>{row.firstname} {row.lastname}</td>
                          <td>{row.belts}</td>
                          <td>{moment(row.date_end).format('MM/DD/YYYY')}</td>
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </Container>
      </>
    );
  }
}
export default ReportContract;
