import React from "react";
import ReactDOM from "react-dom";

import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import "bootstrap/dist/css/bootstrap.min.css";
import "./assets/css/animate.min.css";
import "./assets/scss/dashboard.scss?v=2.0.0";
import "./assets/css/demo.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import 'react-notifications-component/dist/theme.css'

import AdminLayout from "./layouts/Admin.js";
import Home from './views/Home';
import SelectStaff from './views/Select/staff';
import SelectStudent from './views/Select/student';
import CheckStaff from './views/Check/staff';
import CheckStudent from './views/Check/student';

import reportWebVitals from './reportWebVitals';

ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route path="/admin" render={(props) => <AdminLayout {...props} />} />
      <Route exact path="/" component={Home} />
      <Route path='/select/staff' component={SelectStaff} />
      <Route path='/select/student' component={SelectStudent} />
      <Route path='/check/staff' component={CheckStaff} />
      <Route path='/check/student' component={CheckStudent} />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
