import React, { useState, useEffect } from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import { Form, Container, Button, Row, Col } from 'react-bootstrap';
import ReactNotification from 'react-notifications-component'

import MainNavbar from "../../components/Navbars/Navbar";
import { postRequest, URLS } from '../../query';

const SelectStudent = ({ history }) => {
  const [firstCharacter, setFirstCharacter] = useState('A');
  const [studentList, setStudentList] = useState([]);
  const [filteredList, setFilteredList] = useState([]);

  useEffect(() => {
    fetchList();
  }, []);

  const onChangeFirstCharacter = (c) => {
    if (c !== firstCharacter) {
      setFirstCharacter(c);
      setFilteredList(studentList.filter(s => s.firstname[0].toUpperCase() === c));
    }
  }

  const goBack = () => {
    history.goBack();
  }

  const fetchList = () => {
    let loc_id = localStorage.getItem('location');
    postRequest(URLS.student_getall, { loc_id, for: 'front', is_active: true })
    .then(res => {
      if (res.data.success) {
        let list = res.data.result;
        setStudentList(list);
        setFilteredList(list.filter(s => s.firstname[0].toUpperCase() === firstCharacter));
      }
    }, e => {
      console.log(e);
    });
  }

  return (
    <>
    <MainNavbar />
    <ReactNotification />
    <div className='content-wrapper'>
      <Container className="mt-3">
        <Row>
          <Col className="d-flex align-items-center mb-4">
            <h5>What letter does your first name begin with?</h5>
            <Button variant="outline-secondary" className="ml-auto" onClick={goBack}>Start Over</Button>
          </Col>
        </Row>
        <Row>
          <Col className="d-md-none">
            <Form.Control as="select" value={firstCharacter} className="mb-3 w-100"
            onChange={e => onChangeFirstCharacter(e.target.value)}>
              {
                'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('').map(c => (
                  <option key={c} value={c}>{c}</option>
                ))
              }
            </Form.Control>
          </Col>
          <Col className="d-none d-md-block">
          {
            'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('').map(c => (
              <Button key={c} variant="outline-secondary" className='mx-2 mb-3 px-0' 
              style={{ width: 44, height: 44 }}
              active={c === firstCharacter}
              onClick={() => onChangeFirstCharacter(c)}>
                {c}
              </Button>
            ))
          }
          </Col>
        </Row>
        <Row>
          <Col>
          <table className="w-100">
            <tbody>
              {filteredList.map(s => (
                <tr key={s._id}>
                  <td><NavLink to={'/check/student?id='+s._id} ><strong>{s.firstname} {s.lastname}</strong></NavLink></td>
                  <td>{s.belts}</td>
                </tr>
              ))}
            </tbody>
          </table>
          </Col>
        </Row>
      </Container>
    </div>
    </>
  )
}

export default withRouter(SelectStudent);