import React, { Component } from "react";
import { withRouter, NavLink } from 'react-router-dom';
import moment from "moment";
import axios from "axios";
import ReactNotification, { store } from 'react-notifications-component'
import {
  Container, Row, Col, Form
} from "react-bootstrap";

import { URLS } from "../../query";
import MainNavbar from "../../components/Navbars/Navbar";

class Home extends Component 
{
  constructor(props)
  {
    super(props);

    this.state = {
      current_time: moment().format("HH : mm : ss"),
      location: localStorage.getItem('location') || '',
      locations: [],
      schedules: []
    }
    this.timerInterval = null;
  }

  componentDidMount() {
    let self = this;
    this.timerInterval = setInterval(() => {
      self.setState({ current_time: moment().format("HH : mm : ss")})
    }, 1000);

    axios.post(URLS.location_getall)
    .then(res => {
      if (res.data.success) {
        let state = { locations: res.data.result }
        if (!this.state.location && res.data.result.length) {
          state.location = res.data.result[0]._id;
          localStorage.setItem('location', state.location);
          this.fetchSchedules(state.location);
        } else {
          this.fetchSchedules();
        }
        this.setState(state);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  componentWillUnmount() {
    if (this.timerInterval)
      clearInterval(this.timerInterval);
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onChangeLocation = (e) => {
    this.setState({ location: e.target.value });
    localStorage.setItem('location', e.target.value);
    this.fetchSchedules(e.target.value);
  }

  fetchSchedules = (location) => {
    let loc_id = location || this.state.location;
    if (!loc_id) {
      console.log("location cannot be null");
      return;
    }

    axios.post(URLS.schedule_gettoday, { loc_id })
    .then(res => {
      if (res.data.success) {
        this.setState({ schedules: res.data.result })
      }
    }).catch(e => {
      console.log(e);
    })
  }

  render() {
    let current = moment().format('HH:mm');

    return (
      <>
        <MainNavbar />
        <ReactNotification />
        <div className="content-wrapper">
          <Container className="mt-4">
            <Row>
              <Col xs={12} sm={8}>
                <div className="py-2 d-flex justify-content-around">Today is <span style={{ fontWeight: 500 }}>{moment().format('dddd, MM/DD/YYYY')}</span></div>
                <div className="my-4 d-flex align-items-center justify-content-around">Current Time: <span style={{ backgroundColor: '#19A463', color: 'white', fontSize: 24, padding: '2px 12px' }}>{this.state.current_time}</span></div>
              </Col>
              <Col xs={12} sm={4}>
                <Form.Control as="select" value={this.state.location} className="mb-3"
                onChange={this.onChangeLocation}>
                  {
                    this.state.locations.map(loc => (
                      <option key={loc._id} value={loc._id}>{loc.name}</option>
                    ))
                  }
                </Form.Control>
                <NavLink to="/select/student" className='w-100 btn btn-outline-secondary'>
                  Touch here for Student Check In
                </NavLink>
                <NavLink to="/select/staff" className='my-3 ml-auto w-100 btn btn-outline-secondary'>
                  Touch here for Staff Check In              
                </NavLink>
              </Col>
            </Row>
            <Row>
              <div className="text-center w-100 mt-4" style={{ fontWeight: 'bold' }}>Schedule for check in</div>
              <Col>
                <table className="w-100 mt-4">
                  <tbody>
                  {
                    this.state.schedules.map(sc => (
                      <tr key={sc._id} className={current.localeCompare(sc.time_end) > 0 ? 'd-none' : ''}>
                        <td><strong>{sc.class_name}</strong></td>
                        <td>{sc.time_start}-{sc.time_end}</td>
                        <td className="text-italic">{current.localeCompare(sc.time_start) >= 0 ? 'In progress' : 'Coming up' }</td>
                      </tr>
                    ))
                  }
                  </tbody>
                </table>
              </Col>
            </Row>
          </Container>
        </div>
      </>
    )
  }
}

export default withRouter(Home);