import React, { Component } from "react";
import moment from 'moment';
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Table,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { ExportToCsv } from 'export-to-csv';
import { postRequest, URLS } from "../../query";
import { getWithExpiry, lowerBoundIndex } from "../../function";

class ReportBirthday extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      students: [],
      filtered: [],
      day_start: moment().format('YYYY-MM-DD'),
      day_end: moment().format('YYYY-MM-DD'),
      token_info: getWithExpiry('token_info')
    }
  }

  componentDidMount() {
    postRequest(URLS.student_getall, { token: this.state.token_info })
    .then(res => {
      if (res.data.success) {
        let data = res.data.result;
        this.setState({ students: data.sort((a, b)  => {
          let birthday_a = moment(a.birthday);
          let birthday_b = moment(b.birthday);
          if (birthday_a.month() !== birthday_b.month()) {
            return birthday_a.month() - birthday_b.month();
          } else {
            return birthday_a.date() - birthday_b.date();
          }
        }) });
      } else {
        console.log(res.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  handleClickView = () => {
    if (this.state.day_start.localeCompare(this.state.day_end) > 0) {
      this.setState({ filtered: [] });
      return;
    }

    let date_start = moment(this.state.day_start).toISOString();
    let date_end = moment(this.state.day_end).add(1, 'days').toISOString();
    const comp_func = (a, b) => {
      if (moment(a).month() !== moment(b).month())
        return moment(a).month() - moment(b).month();
      return moment(a).date() - moment(b).date();
    }

    const change_year_func = (s, year) => {
      let ns = Object.assign({}, s);
      ns.birthday = moment(s.birthday).set('year', year); 
      return ns;
    }

    let lo = lowerBoundIndex(this.state.students, 'birthday', date_start, comp_func);
    let hi = lowerBoundIndex(this.state.students, 'birthday', date_end, comp_func);
    let year = moment(this.state.day_start).year();

    if (moment(this.state.day_start).year() === moment(this.state.day_end).year()) {
      // same year, choose between two dates
      this.setState({ filtered: this.state.students.slice(lo, hi).map(s => change_year_func(s, year))});
    }
    else {
      let filtered = this.state.students.slice(lo).map(s => change_year_func(s, year));
      let year_end = moment(this.state.day_end).year();
      for (let i = year+1; i<year_end; i++) {
        let arr = this.state.students.map(s => change_year_func(s, i));
        filtered = filtered.concat(arr);
      }
      filtered = filtered.concat(this.state.students.slice(0, hi).map(s => change_year_func(s, year_end)));
      this.setState({ filtered });
    }
  }

  handleExport = () => {
    const options = { 
      filename: 'Birthday Report',
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalSeparator: '.',
      showLabels: true, 
      showTitle: true,
      title: 'Student Birthday Report (' + moment(this.state.day_start).format('MM/DD/YYYY') + ' - ' + moment(this.state.day_end).format('MM/DD/YYYY') + ')',
      useTextFile: false,
      useBom: true,
      headers: ['Name', 'Birthday', 'Class']
    };

    const csvExporter = new ExportToCsv(options);
    csvExporter.generateCsv(this.state.filtered.map(s => ({ 
      name: s.firstname + ' ' + s.lastname,
      birthday: moment(s.birthday).format('MM/DD/YYYY'),
      class_names: s.class_names
    })));
  }

  render()
  {
    return (
      <>
      <Container>
        <Card>
          <Card.Body>
            <Row>
              <Col xs={12} sm={4} md={3}>
                <Form.Group>
                  <label>From</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_start} required onChange={e => this.setState({ day_start: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs={12} sm={4} md={3}>
                <Form.Group>
                  <label>To</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_end} required onChange={e => this.setState({ day_end: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs={12} sm="4" md="6" className='d-flex align-items-end'>
                <Form.Group>
                  <Button className="btn-fill mr-2" variant='secondary' onClick={this.handleClickView}>View</Button>
                  <Button className="btn-fill" variant='info' onClick={this.handleExport}>Export</Button>
                </Form.Group>
              </Col>
              <Col>
                <div className="separator mt-2 mb-4"></div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p>Student Birthday Report ({moment(this.state.day_start).format('MM/DD/YYYY')} - {moment(this.state.day_end).format('MM/DD/YYYY')})</p>
                <Table bordered>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Birthday</th>
                      <th>Class</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.filtered.map((row, idx) => (
                        <tr key={'row_'+idx}>
                          <td>{row.firstname} {row.lastname}</td>
                          <td>{moment(row.birthday).format('MM/DD/YYYY')}</td>
                          <td>{row.class_names}</td>
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </Container>
      </>
    );
  }
}
export default ReportBirthday;
