import React, { Component } from "react";
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { postRequest, URLS } from "../../query";
import { getWithExpiry } from "../../function";

class Addclass extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      token_info: getWithExpiry('token_info'),
      name: "",
      is_regular: false,
      is_testing: false,
      towards_next: false,
      is_active: false,
      belt_group: "",
      // belt_color: "",

      beltgroups: [],
      allbelts: [],
      activebelts: [],
    }
  }

  componentDidMount()
  {
    Promise.all([postRequest(URLS.beltgroup_getall), postRequest(URLS.belt_getall)])
    .then(([resp1, resp2]) => {
      var belts = [];
      var groups = [];
      if (resp1.data.success) {
        groups = resp1.data.result;
      }
      if (resp2.data.success) {
        belts = resp2.data.result;
      }

      var current_group = groups.length > 0 ? groups[0]._id : "";
      var activebelts = belts.filter(belt => belt.group == current_group);
      this.setState({
        beltgroups: groups,
        allbelts: belts,
        activebelts,
        belt_group: current_group,
        // belt_color: activebelts[0]._id
      })
    }).catch(e => {
      console.log(e);
      this.showMessage("Failed to get data");
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onCloseClick = () => {
    this.props.history.goBack();
  }

  saveCurrent = (nexturl) => {
    postRequest(URLS.class_add, {
      name: this.state.name,
      is_regular: this.state.is_regular,
      is_active: this.state.is_active,
      is_testing: this.state.is_testing,
      towards_next: this.state.towards_next,
      belt_group: this.state.belt_group,
      // belt_color: this.state.belt_color,
      token: this.state.token_info
    }).then(resp => {
      if (resp.data.success) {
        var data_id = resp.data.id;
        if (nexturl) this.props.history.replace(nexturl+'?id='+data_id);
        else this.props.history.goBack();
      }
      else {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to save')
    });
  }

  onSaveClick = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent();
  }

  onShowSchedule = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent('/admin/class/schedule');
  }

  onBeltGroupChanged = (e) => {
    var beltgroup_id = e.target.value;
    var activebelts = this.state.allbelts.filter(belt => belt.group == beltgroup_id);
    // var belt_color = activebelts.length > 0 ? activebelts[0]._id : "";

    this.setState({ 
      belt_group: beltgroup_id ,
      activebelts,
      // belt_color
    })
  }

  render()
  {
    return (
      <>
      <Container>
        <Form id='form'>
          <Row>
            <Col>
              <Card>
                <Card.Header>
                  <Card.Title as="h4">Class Setup</Card.Title>
                </Card.Header>
                <Card.Body>
                  <Row>
                    <Col md="3" sm="4" xs="12">
                      <Form.Group>
                        <label>Class name</label>
                        <Form.Control
                          placeholder="Class Name"
                          type="text"
                          required
                          value={this.state.name}
                          onChange={e => this.setState({ name: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Form.Group className='horizontal-centering-container mt-4'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_regular} onChange={e => this.setState({ is_regular: e.target.checked })} />
                        <label>Is this a regular/weekly class?</label>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_testing} onChange={e => this.setState({ is_testing: e.target.checked })} />
                        <label>Is there testing?</label>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.towards_next} onChange={e => this.setState({ towards_next: e.target.checked })} />
                        <label>Does attendance count towards next belt?</label>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_active} onChange={e => this.setState({ is_active: e.target.checked })} />
                        <label>Is this an active class?</label>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="3" sm="4" xs="12">
                      <Form.Group>
                        <label>Belt Group</label>
                        <Form.Control
                          as="select"
                          required
                          value={this.state.belt_group}
                          onChange={this.onBeltGroupChanged}
                        >
                          {
                            this.state.beltgroups.map((group, key) => (
                              <option key={group._id} value={group._id}>{group.name}</option>
                            ))
                          }
                        </Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  {/* <Row>
                    <Col md="3" sm="4" xs="12">
                      <Form.Group>
                        <label>Schedule Color</label>
                        <Form.Control
                          as='select'
                          required
                          value={this.state.belt_color}
                          onChange={e => this.setState({ belt_color: e.target.value })}
                        >
                          { this.state.activebelts.map((belt, key) => (
                              <option key={belt._id} value={belt._id}>{belt.name}</option>
                            ))
                          }
                        </Form.Control>
                      </Form.Group>
                    </Col>
                  </Row> */}
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Button onClick={this.onSaveClick} className="btn-fill float-right ml-auto mr-1" type="submit" variant="info" id='btn-submit'>Save</Button>
            <Button onClick={this.onCloseClick} className="float-right mr-1" variant="outline-secondary">Close this view</Button>
            <Button onClick={this.onShowSchedule} className="float-right" variant="outline-secondary" type="submit">Show Schedule</Button>
          </Row>
        </Form>
      </Container>
      </>
    );
  }
}
export default Addclass;
