import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import ReactNotification, { store } from 'react-notifications-component'

import MainNavbar from "../../components/Navbars/Navbar";
import { postRequest, URLS } from '../../query';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { getMinutes } from '../../function';

const CheckStaff = ({ history }) => {
  const [info, setInfo] = useState({});
  const [schedules, setSchedules] = useState([]);
  const [selectedIds, setSelectedIds] = useState([]);
  const [start, setStart] = useState('00:00');
  const [stop, setStop] = useState('00:00');
  const [processing, setProcessing] = useState(false);

  useEffect(() => {
    let loc_id = localStorage.getItem('location');
    let staff_id = '';
    let searchParams = new URLSearchParams(window.location.search);
    searchParams.has('id') && (staff_id = searchParams.get('id'));

    Promise.all([postRequest(URLS.staff_get, { loc_id, for: 'front', _id: staff_id }),
                postRequest(URLS.schedule_gettoday, { loc_id })]) 
    .then(([res1, res2]) => {
      if (res1.data.success) {
        setInfo(res1.data.result);
      }
      if (res2.data.success) {
        setSchedules(res2.data.result);
      }
    }).catch(e => {
      console.log(e);
    })
  }, []);

  const handleCheckboxClicked = (id, checked) => {
    let new_selected = Object.assign([], selectedIds);

    if (checked && new_selected.indexOf(id) === -1) {
      new_selected.push(id);
    } else if (!checked && new_selected.indexOf(id) !== -1) {
      new_selected.splice(new_selected.indexOf(id), 1);
    }
    setSelectedIds(new_selected);
  }

  const handleCheck = () => {
    let total_time = getMinutes(stop) - getMinutes(start);
    if (total_time < 0) {
      showMessage('Invalid additional time interval.')
      return;
    }

    setProcessing(true);
    let works = [];
    for (let schedule of schedules) {
      if (selectedIds.indexOf(schedule._id) !== -1 ) {
        works.push({ _id: schedule._id, class_name: schedule.class_name, time_start: schedule.time_start, time_end: schedule.time_end });
        let interval = getMinutes(schedule.time_end) - getMinutes(schedule.time_start);
        total_time += interval;
      }
    }

    postRequest(URLS.check_staff_add, {
      staff: info._id,
      schedules: works,
      additional: { time_start: start, time_end: stop },
      total_time
    }).then(res => {
      setProcessing(false);
      if (res.data.success) {
        showMessage('Success', '', 'success');
        setTimeout(() => {
          history.replace('/')
        }, 1000);
      } else {
        showMessage(res.data.message);
      }
    }, e => {
      console.log(e);
      setProcessing(false);
      showMessage('Failed to check.');
    })
  }

  const showMessage = (msg, title, type) => {
    store.addNotification({
      title: title === undefined ? "Error" : title,
      message: msg,
      type: type || "danger",
      insert: "top",
      container: "top-right",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  return (
    <>
    <MainNavbar />
    <ReactNotification />
    <Container className="mt-4">
      <Row>
        <Col xs={12}>
        <h5>Hello <strong>{info.name}</strong>! Select the class you will be instructing or enter start/stop times then click CHECK IN.</h5>
        </Col>
        <Col xs={12} sm={6} md={7}>
          {
            schedules.map(sc => (
              <div key={sc._id}>
                <input type='checkbox' className="mr-2" onChange={(e) => handleCheckboxClicked(sc._id, e.target.checked)} />
                <label>{sc.class_name} ({sc.time_start} - {sc.time_end})</label>
              </div>
            ))
          }
          {
            schedules.length === 0
            ? 'No schedule today'
            : null
          }
        </Col>
        <Col xs={12} sm={6} md={5}>
          <Form.Group>
            <label>Start</label>
            <Form.Control type="time" step="60" value={start} onChange={e => setStart(e.target.value)}>
            </Form.Control>
          </Form.Group>
          <Form.Group>
            <label>Stop</label>
            <Form.Control type="time" step="60" value={stop} onChange={e => setStop(e.target.value)}>
            </Form.Control>
          </Form.Group>
          <Button variant="outline-secondary" disabled={processing} onClick={handleCheck}>Check in</Button>
          <Button variant="outline-secondary" className="ml-2" onClick={() => history.replace('/')}>Start Over</Button>
        </Col>
        {/* <Col xs={12} sm={6} md={5} className="mt-2" >
        </Col> */}
      </Row>
    </Container>
    </>
  )
}

export default withRouter(CheckStaff);