import React, { Component } from "react";
import { withRouter } from 'react-router-dom';

import ReactNotification from 'react-notifications-component'
import { store } from 'react-notifications-component';

import {
  Button,
  Card,
  Form,
  Toast
} from "react-bootstrap";

import { postRequest, URLS } from "../../query";
import { setWithExpiry } from "../../function";

class Login extends Component 
{
  constructor(props)
  {
    super(props);

    this.state = {
      signing: true,
      email_sign: '',
      password_sign: '',

      name: '',
      password: '',
      confirm_password: ''
    }

    this.signIn = this.signIn.bind(this);
    this.register = this.register.bind(this);
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  signIn(e) {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();

    postRequest(URLS.login, { name: this.state.name, password: this.state.password })
    .then(resp => {
      var data = resp.data;
      if (!data.success) {
        this.showMessage(data.message);
      }
      else {
        setWithExpiry('token_info', data.token, 40000*1000)
        this.props.setLoggedIn && this.props.setLoggedIn(true);
        this.props.history.replace('/admin/students');
      }
    })
    .catch(e => {
      console.log(e);
      this.showMessage(e.message);
    })
  }

  register() {
    postRequest(URLS.register, { 
      name: this.state.name,
      password: this.state.password,
      confirm_password: this.state.confirm_password
    })
    .then(resp => {
      var data = resp.data;
      if (!data.success) {
        this.showMessage(data.message);
      }
      else {
        setWithExpiry('token_info', data.token, 40000*1000)
        this.props.history.replace('/admin/students');
      }
    })
    .catch(e => {
      console.log(e);
      this.showMessage(e.message);
    })
  }

  render() {
    return (
      <div className="vertical-centering-container">
        <ReactNotification />
        <div style={{ marginTop: '10%'}} className='vertical-centering-container'>
          <img src='/logo.gif' className="App-logo" alt="logo" />
          <Card style={{ width: 320 }} className='mt-4'>
            <Card.Body>
              <Card.Title style={{ textAlign: 'center' }}>Martial Arts Admin</Card.Title>
            {/* {
              this.state.signing ?  */}
              <Form id='form'>
                <Form.Group className='mt-4'>
                  <label>Admin Name</label>
                  <Form.Control
                    placeholder="Name"
                    type="text"
                    required
                    value={this.state.name}
                    onChange={e => this.setState({ name: e.target.value })}
                  ></Form.Control>
                </Form.Group>
                <Form.Group>
                  <label>Admin Password</label>
                  <Form.Control
                    placeholder="Password"
                    type="password"
                    required
                    value={this.state.password}
                    onChange={e => this.setState({ password: e.target.value })}
                  ></Form.Control>
                </Form.Group>
                  {/* <div style={{ fontSize: 12, marginTop: 12, display: "flex", alignItems: 'center' }}>
                    Don't have account? 
                    <Button label='Create One' className='p-button-text' style={{ fontSize: 12 }} 
                    onClick={() => this.setState({ signing: false })} />
                  </div> */}
                <Button onClick={this.signIn} className="float-right btn-fill" variant="primary" type='submit'>Sign In</Button>
                {/* : */}
                {/* // <div style={{ display: "flex", flexDirection: "column", alignItems: 'center' }}>
                //   <span className='p-float-label' style={styles.full_width}>
                //     <Form.Text id='firstname' style={styles.full_width} value={this.state.firstname} onChange={(e) => this.setState({ firstname: e.target.value })} />
                //     <label htmlFor='firstname'>Firstname</label>
                //   </span>
                //   <span className='p-float-label' style={{ marginTop: 36, ...styles.full_width }}>
                //     <Form.Text id='lastname' style={styles.full_width} value={this.state.lastname} onChange={(e) => this.setState({ lastname: e.target.value })} />
                //     <label htmlFor='lastname'>Lastname</label>
                //   </span>
                //   <span className='p-float-label' style={{ marginTop: 36, ...styles.full_width }}>
                //     <Form.Text id='inputEmailRegister' style={styles.full_width} value={this.state.email} onChange={(e) => this.setState({ email: e.target.value })} />
                //     <label htmlFor='inputEmailRegister'>Email</label>
                //   </span>
                //   <span className='p-float-label' style={{ marginTop: 36, ...styles.full_width }}>
                //     <Form.Password id='inputPasswordRegister' style={styles.full_width} feedback={false}
                //     value={this.state.password} onChange={(e) => this.setState({ password: e.target.value })} />
                //     <label htmlFor='inputPasswordRegister'>Password</label>
                //   </span>
                //   <span className='p-float-label' style={{ marginTop: 36, ...styles.full_width }}>
                //     <Form.Password id='confirmpassword' style={styles.full_width} feedback={false}
                //     value={this.state.confirm_password} onChange={(e) => this.setState({ confirm_password: e.target.value })} />
                //     <label htmlFor='confirmpassword'>Confirm Password</label>
                //   </span>
                //   <Button label='Register' onClick={this.register} style={{ marginTop: 24 }} />
                //   <div style={{ fontSize: 12, marginTop: 12, display: "flex", alignItems: 'center' }}>
                //     Have already an account? 
                //     <Button label='Sign In' className='p-button-text' style={{ fontSize: 12 }} 
                //     onClick={() => this.setState({ signing: true })} />
                //   </div>
                // </div>
              // } */}
              </Form>
            </Card.Body>
          </Card>
        </div>
      </div>
    );
  }
}

export default withRouter(Login);