import React, { Component } from "react";
import moment from 'moment';
import {
  Alert,
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  OverlayTrigger,
  Tooltip,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { getWithExpiry, repeatedArray } from '../../function';
import { postRequest, postForm, URLS } from "../../query";

class Addstudent extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      firstname: '',
      lastname: '',
      birthday: moment().format("YYYY-MM-DD"),
      started: moment().format("YYYY-MM-DD"),
      endDate: moment().format("YYYY-MM-DD"),
      is_active: false,
      avatar: '',
      image_url: '',
      contact_person: "",
      contact_email: '',
      contact_phone: '',
      is_trial: false,
      trial_start: moment().format("YYYY-MM-DD"),
      trial_end: moment().format("YYYY-MM-DD"),
      trial_num_classes: 0,
      levels: [],
      notes: '',

      token_info: getWithExpiry('token_info'),
      belts: [],
      beltgroups: []
    }
  }

  componentDidMount()
  {
    Promise.all([postRequest(URLS.belt_getall), postRequest(URLS.beltgroup_getall)])
    .then(([res1, res2]) => {
      var belts = [];
      var beltgroups = [];

      if (res1.data.success) {
        belts = res1.data.result;
      }
      else
      {
        this.showMessage(res1.data.message);
      }
      if (res2.data.success) {
        beltgroups = res2.data.result;
      }
      else
      {
        this.showMessage(res2.data.message);
      }

      this.setState({ belts, beltgroups, levels: repeatedArray("", beltgroups.length)});
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onCloseClick = () => {
    this.props.history.goBack();
  }

  saveCurrent = (nexturl) => {
    postForm(URLS.student_add, {
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      birthday: this.state.birthday,
      date_started: this.state.started,
      date_end: this.state.endDate,
      is_active: this.state.is_active,
      avatar: this.state.avatar,
      notes: this.state.notes,
      levels: this.state.levels,
      contact_person: this.state.contact_person,
      contact_email: this.state.contact_email,
      contact_phone: this.state.contact_phone,
      is_trial: this.state.is_trial,
      trial_start: this.state.trial_start,
      trial_end: this.state.trial_end,
      trial_num_classes: this.state.trial_num_classes,
      token: this.state.token_info,
    }).then(resp => {
      if (resp.data.success) {
        if (nexturl) this.props.history.replace(nexturl);
        else this.props.history.goBack();
      }
      else {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to save')
    });
  }

  onSaveClick = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent();
  }

  onUpdatePhotoClick = () => {
    document.getElementById('image-file').click();
  }

  onImageSelected = (e) => {
    var self = this;
    var files = e.target.files;
    var image_base64;
    var reader = new FileReader(); 
    reader.onload = (re) => {
      image_base64 = re.target.result;
      self.setState({ avatar: files[0], image_url: image_base64 });
    };

    reader.readAsDataURL(files[0]);
  }

  onLevelChanged = (idx, lvl) => {
    var levels = Object.assign([], this.state.levels);
    levels[idx] = lvl;
    this.setState({ levels });
  }

  onAddClassClick = (e) => {
    var form = document.getElementById('form');
    if (!form.checkValidity())
    {
      return;
    }

    e.preventDefault();
    this.saveCurrent('/admin/students/addclass?id='+this.state._id);
  }
  render()
  {
    return (
      <>
      <Container>
        <Form id='form'>
          <Row>
            <Col sm={8} xs={12}>
              <Card>
                <Card.Header>
                  <Card.Title as="h4">Add a student</Card.Title>
                </Card.Header>
                <Card.Body>
                  <Row>
                    <Col md="6">
                      <Form.Group>
                        <label>First Name</label>
                        <Form.Control
                          placeholder="First Name"
                          type="text"
                          required
                          value={this.state.firstname}
                          onChange={e => this.setState({ firstname: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                    <Col md="6">
                      <Form.Group>
                        <label>Last Name</label>
                        <Form.Control
                          placeholder="Last Name"
                          type="text"
                          required
                          value={this.state.lastname}
                          onChange={e => this.setState({ lastname: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="6">
                      <Form.Group>
                        <label>Birthday</label>
                        <div className='full-width'>
                          <Form.Control type="date" value={this.state.birthday} required onChange={e => this.setState({ birthday: e.target.value })}>
                          </Form.Control>
                        </div>
                      </Form.Group>
                      <Form.Group>
                        <label>Date Started</label>
                        <div className='full-width'>
                          <Form.Control type="date" value={this.state.started} onChange={e => this.setState({ started: e.target.value })}>
                          </Form.Control>
                        </div>
                      </Form.Group>
                      <Form.Group>
                        <label>Contract End Date</label>
                        <div className='full-width'>
                          <Form.Control type="date" value={this.state.endDate} onChange={e => this.setState({ endDate: e.target.value })}>
                          </Form.Control>
                        </div>
                      </Form.Group>
                      <Form.Group className='horizontal-centering-container'>
                        <input type='checkbox' className='mb-2 mr-2' checked={this.state.is_active} onChange={e => this.setState({ is_active: e.target.checked })} />
                        <label>Is active?</label>
                      </Form.Group>
                    </Col>
                    <Col md="6">
                      <Card className="card-user">
                        <div className="card-body vertical-centering-container">
                        <a href="#pablo" onClick={(e) => e.preventDefault()} className='mt-3'>
                          <img
                            alt=""
                            src={this.state.image_url}
                            className="student_avatar border-gray"
                          ></img>
                        </a>
                        <input type='file' className='hidden' id='image-file' accept="image/*" onChange={this.onImageSelected} />
                        <Button variant='outline-secondary' onClick={this.onUpdatePhotoClick} className='mt-auto mb-3'>Update Photo</Button>
                        </div>
                      </Card>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="4">
                      <label>Contact Person</label>
                      <Form.Control
                        placeholder="Contact Person"
                        type="text"
                        value={this.state.contact_person}
                        onChange={e => this.setState({ contact_person: e.target.value })}
                      ></Form.Control>
                    </Col>
                    <Col md="4">
                      <label>Contact Email</label>
                      <Form.Control
                        placeholder="Contact Email"
                        type="email"
                        value={this.state.contact_email}
                        onChange={e => this.setState({ contact_email: e.target.value })}
                      ></Form.Control>
                    </Col>
                    <Col md="4">
                      <label>Contact Phone</label>
                      <Form.Control
                        placeholder="Contact Phone"
                        type="phone"
                        value={this.state.contact_phone}
                        onChange={e => this.setState({ contact_phone: e.target.value })}
                      ></Form.Control>
                    </Col>
                  </Row>
                  <Row>
                    <Col className="pr-1 pt-4" md="4">
                      <label>Current Class(es):</label>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12" className="mt-2">
                      <Form.Group>
                        <label>Notes</label>
                        <Form.Control
                          cols="80"
                          placeholder="Please add notes..."
                          rows="4"
                          as="textarea"
                          value={this.state.notes}
                          onChange={e => this.setState({ notes: e.target.value })}
                        ></Form.Control>
                      </Form.Group>
                    </Col>
                  </Row>
                </Card.Body>
              </Card>
            </Col>
            <Col sm={4} xs={12}>
              <Card className="card-user">
                <Card.Header>
                  Which martial arts is this student studying?
                </Card.Header>
                <Card.Body>
                  {
                    this.state.beltgroups.map((btgrp, idx) => (
                      <Form.Group key={btgrp._id}>
                        <label>{btgrp.name}</label>
                        <Form.Control
                          as="select"
                          value={this.state.levels[idx]}
                          onChange={e => this.onLevelChanged(idx, e.target.value)}
                        >
                          <option value=''>None</option>
                          { this.state.belts.filter(belt => belt.group == btgrp._id).map(bt => (
                            <option key={bt._id} value={bt._id}>{bt.name}</option>
                          ))}
                        </Form.Control>
                      </Form.Group>
                    ))
                  }

                  <div className='vertical-centering-container full-width'>
                    {/* <Button variant='light' className='float-center'>Manage type of class</Button> */}
                  </div>
                </Card.Body>
              </Card>
              <Card className="card-user">
                <Card.Header>
                  Trial Student
                </Card.Header>
                <Card.Body>
                  <Form.Group>
                    <div className='horizontal-centering-container'>
                      <label>Is this a trial?</label>
                      <input type='checkbox' className='ml-2 mb-2' checked={this.state.is_trial} onChange={e => this.setState({ is_trial: e.target.checked })} />
                    </div>
                  </Form.Group>
                  <Form.Group>
                    <label>Starts</label>
                    <div className='full-width'>
                      <Form.Control type="date" value={this.state.trial_start} onChange={e => this.setState({ trial_start: e.target.value })}>
                      </Form.Control>
                    </div>
                  </Form.Group>
                  <Form.Group>
                    <label>Ends</label>
                    <div className='full-width'>
                      <Form.Control type="date" value={this.state.trial_end} onChange={e => this.setState({ trial_end: e.target.value })}>
                      </Form.Control>
                    </div>
                  </Form.Group>
                  <Form.Group>
                    <label># Classes</label>
                    <Form.Control
                      value={this.state.trial_num_classes}
                      placeholder=""
                      type="text"
                      onChange={e => this.setState({ trial_num_classes: e.target.value })}
                    ></Form.Control>
                  </Form.Group>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Button onClick={this.onSaveClick} className="btn-fill float-right ml-auto mr-1" type="submit" variant="info" id='btn-submit'>Save</Button>
            {/* <Button onClick={this.onNotesClick} className="float-right mr-1" variant="light">Notes</Button> */}
            <Button onClick={this.onAddClassClick} className="float-right mr-1" variant="outline-secondary" type='submit'>Add a class</Button>
            {/* <Button onClick={this.onDeleteClick} className="float-right mr-1" variant="danger">Delete</Button> */}
            <Button onClick={this.onCloseClick} className="float-right" variant="outline-secondary">Close this view</Button>
          </Row>
        </Form>
      </Container>
      </>
    );
  }
}
export default Addstudent;
