import React, { Component } from "react";
import moment from 'moment';
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Table,
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { ExportToCsv } from 'export-to-csv';
import { postRequest, URLS } from "../../query";
import { getMinutes, getWithExpiry, lowerBoundIndex, minutes2TimeString } from "../../function";

class ReportStaff extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      staffs: [],
      staff: '',
      logs: [],
      filtered: [],
      day_start: moment().format('YYYY-MM-DD'),
      day_end: moment().format('YYYY-MM-DD'),
      token_info: getWithExpiry('token_info'),

      staff_name: '',
      gen_day_start: moment().format('YYYY-MM-DD'),
      gen_day_end: moment().format('YYYY-MM-DD'),
      gen_total: ''
    }
  }

  componentDidMount() {
    postRequest(URLS.staff_getall, { token: this.state.token_info })
    .then(res => {
      if (res.data.success) {
        let data = res.data.result;
        this.setState({ staffs: data, staff: data[0]?._id, staff_name: data[0]?.name });
        this.fetchStaffData(data[0]?._id);
      } else {
        console.log(res.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  onStaffChanged = (s) => {
    this.setState({ staff: s })
    this.fetchStaffData(s);
  }

  fetchStaffData = (s_id) => {
    let staff_id = s_id || this.state.staff;
    postRequest(URLS.check_staff_get, { token: this.state.token_info, staff: staff_id })
    .then(res => {
      if (res.data.success) {
        this.setState({ logs: res.data.result });
      } else {
        this.showMessage(res.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  handleClickView = () => {
    if (this.state.day_start.localeCompare(this.state.day_end) > 0) {
      this.setState({ filtered: [] });
      return;
    }

    let date_start = moment(this.state.day_start).toISOString();
    let date_end = moment(this.state.day_end).add(1, 'days').toISOString();
    const comp_func = (a, b) => a.localeCompare(b)

    let lo = lowerBoundIndex(this.state.logs, 'createdAt', date_start, comp_func);
    let hi = lowerBoundIndex(this.state.logs, 'createdAt', date_end, comp_func);
    let staff = this.state.staffs.find(st => st._id === this.state.staff);
    let total = 0;
    let filtered = this.state.logs.slice(lo, hi).map(log => {
      let arr = log.schedules.map(sc => ({
        date: moment(log.createdAt).format('MM/DD/YYYY'),
        day: moment(log.createdAt).format('dddd'),
        interval: sc.time_start + ' - ' + sc.time_end,
        class_name: sc.class_name,
        total: minutes2TimeString(getMinutes(sc.time_end)-getMinutes(sc.time_start))
      }))
      if (!!log.additional && log.additional.time_start.localeCompare(log.additional.time_end) < 0)
      {
        arr.push({
          date: moment(log.createdAt).format('MM/DD/YYYY'),
          day: moment(log.createdAt).format('dddd'),
          interval: log.additional.time_start + ' - ' + log.additional.time_end,
          class_name: 'None',
          total: minutes2TimeString(getMinutes(log.additional.time_end)-getMinutes(log.additional.time_start))
        })
      }
      total += log.total_time;
      return arr;
    });

    filtered = filtered.flat().sort((a, b) => {
      if (!a.date.localeCompare(b.date)) {
        return a.interval.localeCompare(b.interval);
      }
      return a.date.localeCompare(b.date);
    })
    this.setState({ 
      filtered,
      gen_day_start: this.state.day_start,
      gen_day_end: this.state.day_end,
      staff_name: staff?.name,
      gen_total: total
    });
  }

  handleExport = () => {
    const options = { 
      filename: 'Staff Attendance',
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalSeparator: '.',
      showLabels: true, 
      showTitle: true,
      title: 'Staff Attendance\n' + this.state.staff_name + ' (' + moment(this.state.day_start).format('MM/DD/YYYY') + ' - ' + moment(this.state.day_end).format('MM/DD/YYYY') + ')',
      useTextFile: false,
      useBom: true,
      headers: ['Date', 'Day', 'In/Out', 'Class', 'Total']
    };

    const csvExporter = new ExportToCsv(options);
    let data = Object.assign([], this.state.filtered);
    data.push({ date: '', day: '', interval: '', class_name: 'Total', total: minutes2TimeString(this.state.gen_total) });
    csvExporter.generateCsv(data);
  }

  render()
  {
    return (
      <>
      <Container>
        <Card>
          <Card.Body>
            <Card.Title className="mb-2">
              Display Staff Attendance
            </Card.Title>
            <Row>
              <Col xs={12} sm={6} lg={3}>
                <Form.Group>
                  <label>From</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_start} required onChange={e => this.setState({ day_start: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs={12} sm={6} lg={3}>
                <Form.Group>
                  <label>To</label>
                  <div className='full-width'>
                    <Form.Control type="date" value={this.state.day_end} required onChange={e => this.setState({ day_end: e.target.value })}>
                    </Form.Control>
                  </div>
                </Form.Group>
              </Col>
              <Col xs="12" sm="6" lg="3">
                <Form.Group>
                  <label>Staff</label>
                  <Form.Control
                    as="select"
                    value={this.state.staff}
                    onChange={e => this.onStaffChanged(e.target.value)}
                  >
                    { this.state.staffs.map(staff => (
                      <option key={staff._id} value={staff._id}>{staff.name}</option>
                    ))}
                  </Form.Control>
                </Form.Group>
              </Col>
              <Col xs={12} sm="6" lg="3" className='d-flex align-items-end'>
                <Form.Group>
                  <Button className="btn-fill mr-2" variant='secondary' onClick={this.handleClickView}>View</Button>
                  <Button className="btn-fill" variant='info' onClick={this.handleExport}>Export</Button>
                </Form.Group>
              </Col>
              <Col>
                <div className="separator mt-2 mb-4"></div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p>Staff Attendance Report <br/>{this.state.staff_name} ({moment(this.state.gen_day_start).format('MM/DD/YYYY')} - {moment(this.state.gen_day_end).format('MM/DD/YYYY')})</p>
                <Table bordered>
                  <thead>
                    <tr>
                      <th>Date</th>
                      <th>Day</th>
                      <th>In/Out</th>
                      <th>Class</th>
                      <th>Total</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.filtered.map((row, idx) => (
                        <tr key={'row_'+idx}>
                          <td>{row.date}</td>
                          <td>{row.day}</td>
                          <td>{row.interval}</td>
                          <td>{row.class_name}</td>
                          <td>{row.total}</td>
                        </tr>
                      ))
                    }
                    <tr>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td>Total</td>
                      <td>{minutes2TimeString(this.state.gen_total)}</td>
                    </tr>
                  </tbody>
                </Table>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </Container>
      </>
    );
  }
}
export default ReportStaff;
