import React, { Component } from "react";

import {
  Button,
  ButtonGroup,
  Card,
  Table,
  Container,
  Row,
  Col,
  ToggleButton,
} from "react-bootstrap";

import { store } from 'react-notifications-component';
import { postRequest, URLS } from "../../query";
import { getWithExpiry } from '../../function';

class Class extends Component {
  constructor(props)
  {
    super(props);
    this.state = {
      view_regular: true,
      active_only: true,
      token_info: getWithExpiry('token_info'),
      all_classes: [],
      class_list: []
    }
  }

  componentDidMount()
  {
    postRequest(URLS.class_getall, { token: this.state.token_info })
    .then(resp => {
      if (resp.data.success)
      {
        var class_list = resp.data.result.filter(cl => cl.is_regular == this.state.view_regular);
        this.setState({ class_list, all_classes: resp.data.result })
      }
      else
      {
        this.showMessage(resp.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  onToggleActive = (e) => {
    this.setState({
      active_only: e.target.checked
    })
  }

  
  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  viewCalendar = () => {
    this.props.history.push('/admin/class/calendar');
  }

  toggleViewRegular = () => {
    var view_regular = !this.state.view_regular;
    var class_list = this.state.all_classes.filter(cl => cl.is_regular == view_regular);

    this.setState({ view_regular, class_list });
  }

  addNewClass = () => {
    this.props.history.push('/admin/class/add');
  }

  render()
  {
    var class_list = this.state.class_list;
    if (this.state.active_only)
      class_list = class_list.filter(cl => cl.is_active);
    return (
      <>
        <div className='horizontal-centering-container pl-3 pr-3'>
          <div className=''>{ this.state.view_regular ? 'List of Weekly Classes' : 'List of Special Classes'}</div>
          <div className='horizontal-centering-container' style={{ marginLeft: 'auto', marginRight: 0 }}>
            <div className='mr-2'>Displaying</div>
            <ButtonGroup toggle>
            <ToggleButton type='checkbox' variant='outline-secondary' checked={this.state.active_only}
            onChange={this.onToggleActive}>Active Only
            </ToggleButton>
            </ButtonGroup>
          </div>
        </div>
        <Container fluid>
          <Card className="strpied-tabled-with-hover mt-4">
            <Card.Header>
              <Card.Title as="h4">Class</Card.Title>
            </Card.Header>
            <Card.Body className="table-full-width table-responsive px-4">
              <Row>
              {
                class_list.map((cl, key) => (
                  <Col key={cl._id} md="3" sm="6" xs="12">
                    <a href={'/admin/class/edit?id='+cl._id}>{cl.name}</a>
                  </Col>
                ))
              }
              </Row>
            </Card.Body>
          </Card>
          <Row>
            <Col md="12">
              <Button onClick={this.toggleViewRegular} className="float-right" variant="outline-secondary">
                { this.state.view_regular ? 'View special classes' : 'View regular classes' }
              </Button>
              <Button onClick={this.viewCalendar} className="float-right mr-1" variant="outline-secondary">View calendar</Button>
              <Button onClick={this.addNewClass} className="btn-fill float-right ml-auto mr-1" variant="outline-secondary">Add a new class</Button>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

export default Class;
