import React, { Component } from 'react'

export class CalendarCell extends Component
{
  render()
  {
    return (
      <div className='calendar_float_cell' style={{ position: 'absolute', left: this.props.left, top: this.props.top, height: this.props.bottom-this.props.top, width: this.props.width, backgroundColor: this.props.backgroundColor }}>
        <div>{this.props.name}</div>
        <div>{this.props.time_start} - {this.props.time_end}</div>
      </div>
    )
  }
}