import React, { Component } from "react";
// react-bootstrap components
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Tooltip,
} from "react-bootstrap";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import { getWithExpiry } from "../../function";
import { URLS, postRequest } from "../../query";

const columns = [{
  dataField: 'avatar',
  text: '',
  headerStyle: { width: 56 },
  searchable: false,
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return <img src={URLS.base_url + row.avatar} alt='' className='student_avatar_in_table' />
  }
}, {
  dataField: 'firstname',
  text: 'Name',
  sort: true,
  formatter: function callback(cell, row, rowIndex, formatExtraData) {
    return <a href={'/admin/students/edit?id=' + row._id}>{row.firstname} {row.lastname}</a>
  }
}, {
  dataField: 'belts',
  text: 'Belt',
  searchable: false
}, {
  dataField: 'class_names',
  text: 'Classes',
  searchable: false
}];
const pagination = paginationFactory({
  page: 1,
  alwaysShowAllBtns: true,
  showTotal: true,
  withFirstAndLast: true,
})
const { SearchBar } = Search;

class Student extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      list: [],
      token_info: getWithExpiry('token_info'),
      include_inactive: false
    }
  }

  componentDidMount()
  {
    postRequest(URLS.student_getall, { token: this.state.token_info })
    .then(response => {
      if (response.data.success)
      {
        this.setState({ list: response.data.result });
      } else {
        console.log(response.data.message);
      }
    }).catch(e => {
      console.log(e);
    })
  }

  onAddClick = () => {
    this.props.history.push('/admin/students/add');
  };

  render()
  {
    var students = this.state.list;
    if (!this.state.include_inactive)
    {
      students = students.filter(s => s.is_active );
    }

    return (
      <>
      <ToolkitProvider keyField='_id' data={ students } columns={ columns } search>
        {
          props => (
            <div className="py-4">
              <div
                id="datatable-basic_filter"
                className="dataTables_filter px-4 pb-1 "
              >
                <Row>
                  <Col sm={5} xs={12} className='horizontal-centering-container mb-2'>
                    <label>
                      Name starts with:&nbsp;&nbsp;
                    </label>
                    <SearchBar
                        className="form-control-sm"
                        placeholder=""
                        {...props.searchProps}
                      />
                  </Col>
                  <Col sm={4} xs={12} className='horizontal-centering-container mb-2'>
                    <input type='checkbox' className='mb-2' checked={this.state.include_inactive} onChange={e => this.setState({ include_inactive: e.target.checked})} />
                    <label>&nbsp;Include inactive</label>
                  </Col>
                  <Col sm={3} xs={12} className='horizontal-centering-container mb-2'>
                    <Button onClick={this.onAddClick} variant='secondary' style={{ marginLeft: 'auto', marginRight: 0 }}>Add a student</Button>
                  </Col>
                </Row>
              </div>
              <BootstrapTable
                {...props.baseProps}
                bootstrap4={true}
                pagination={pagination}
                bordered={false}
              />
            </div>
          )
        }
      </ToolkitProvider>
      </>
    );
  }
}

export default Student;
