import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import ReactNotification, { store } from 'react-notifications-component'

import MainNavbar from "../../components/Navbars/Navbar";
import { postRequest, URLS } from '../../query';
import moment from 'moment';
import { Button, Col, Container, Row } from 'react-bootstrap';

const CheckStudent = ({ history }) => {
  const [info, setInfo] = useState({});
  const [schedule, setSchedule] = useState(null);
  const [isWrong, setIsWrong] = useState(false);

  useEffect(() => {
    let loc_id = localStorage.getItem('location');
    let searchParams = new URLSearchParams(window.location.search);
    let stud_id = '';
    searchParams.has('id') && (stud_id = searchParams.get('id'));

    Promise.all([postRequest(URLS.student_get, { loc_id, _id: stud_id, for: 'front' }),
                postRequest(URLS.schedule_gettoday, { loc_id })])
    .then(([res1, res2]) => {
      let schedule_ids = [];

      if (res1.data.success)
      {
        var data = res1.data.result;
        var student = {
          _id: data._id,
          firstname: data.firstname,
          lastname: data.lastname,
          birthday: moment(data.birthday).format("YYYY-MM-DD"),
          is_active: data.is_active,
          image_url: URLS.base_url + data.avatar,
          is_trial: data.is_trial,
          levels: data.levels,
          attendance: data.attendance || [],
        };

        for (let sc of student.attendance)
        {
          schedule_ids = schedule_ids.concat(sc.schedules);
        }
        setInfo(student);
      }
      else
      {
        showMessage(res1.data.message);
      }

      if (res2.data.success) {
        let current = moment().format('HH:mm');
        let list = res2.data.result;
        list = list.filter(sc => current.localeCompare(sc.time_end) < 0 );
        let subscribed_list = list.filter(sc => schedule_ids.indexOf(sc._id) !== -1);
        if (subscribed_list.length) {
          setSchedule(subscribed_list[0]);
          setIsWrong(false);
        } else if (list.length) {
          setSchedule(list[0]);
          setIsWrong(true);
        }
      }
    }).catch(e => {
      console.log(e);
    })
  }, []);

  const handleCheck = () => {
    postRequest(URLS.check_student_add, {
      student: info._id,
      class_id: schedule.class_id,
      class_name: schedule.class_name,
      schedule_id: schedule._id,
      is_wrong: isWrong
    }).then(res => {
      if (res.data.success) {
        showMessage('Success', '', 'success');
        setTimeout(() => {
          history.replace('/')
        }, 1000);
      } else {
        showMessage(res.data.message);
      }
    }, e => {
      console.log(e);
      showMessage('Failed to check.');
    })
  }

  const showMessage = (msg, title, type) => {
    store.addNotification({
      title: title === undefined ? "Error" : title,
      message: msg,
      type: type || "danger",
      insert: "top",
      container: "top-right",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  console.log('09:45'.localeCompare('10:00'), moment().format('HH:mm'))

  return (
    <>
    <MainNavbar />
    <ReactNotification />
    <Container className="mt-4">
      <Row>
        <Col xs={12} sm={4}>
          <img src={info.image_url} className="w-100 mb-4"/>
        </Col>
        <Col xs={12} sm={8} md={4}>
          <h5><strong>{info.firstname} {info.lastname}</strong> is checking in for class:</h5>
          <span>
            {
              schedule ?
              schedule.class_name
              : 'No upcoming class today.'
            }
          </span>
          <div>
            <span style={{ color: '#ff2333', fontSize: 14, fontStyle: 'italic' }} className={schedule && schedule.time_start.localeCompare(moment().format('HH:mm')) <= 0 ? 'mr-2' : 'd-none'}><strong>Late</strong></span>
            <span style={{ color: '#FED800', fontSize: 14, fontStyle: 'italic' }} className={isWrong ? '' : 'd-none'}><strong>Wrong</strong></span>
          </div>
          <Button variant="outline-secondary" className="w-100 mt-2" disabled={!schedule} onClick={handleCheck}>Check in</Button>
          <Button variant="outline-secondary" className="w-100 mt-2" onClick={() => history.replace('/')}>Start Over</Button>
        </Col>
      </Row>
    </Container>
    </>
  )
}

export default withRouter(CheckStudent);