import React, { Component } from "react";
import moment from 'moment';
import {
  Button,
  Card,
  Container,
  Row,
  Col,
  Form,
  Table
} from "react-bootstrap";
import { store } from 'react-notifications-component';
import { postRequest, URLS } from "../../query";
import { getWithExpiry } from "../../function";

class DailyClass extends Component
{
  constructor(props)
  {
    super(props);

    var url = new URL(window.location.href);
    this.state = {
      date: url.searchParams.get('date'),
      token_info: getWithExpiry('token_info'),
      schedules: [], 
      staffs: []
    }
  }

  componentDidMount()
  {
    this.refreshData(new Date(this.state.date));
  }

  showMessage = (msg) => {
    store.addNotification({
      title: "Error",
      message: msg,
      type: "danger",
      insert: "top",
      container: "top-center",
      animationIn: ["animate__animated", "animate__fadeIn"],
      animationOut: ["animate__animated", "animate__fadeOut"],
      dismiss: {
        duration: 3000,
        // onScreen: true
      }
    });
  }

  refreshData = (date) => {
    Promise.all([postRequest(URLS.schedule_getbydate, { token: this.state.token_info, date }),
                postRequest(URLS.staff_getall, { token: this.state.token_info })])
    .then(([res1, res2]) => {
      var staffs = [];
      if (res2.data.success)
      {
        staffs = res2.data.result.filter(st => st.is_active);
      }
      if (res1.data.success)
      {
        var schedules = res1.data.result.filter(cl => cl.class_is_active);
        var schedules = schedules.sort((a, b) => {
          return a.time_start.localeCompare(b.time_start);
        })
        for (var i=0; i<schedules.length; i++)
        {
          var staff_names = [];
          for (var j=0; j<schedules[i].staffs.length; j++)
          {
            var staff = staffs.find(s => s._id == schedules[i].staffs[j]);
            if (staff) staff_names.push(staff.name);
          }
          var staff_str = "";
          if (staff_names.length > 2)
          {
            staff_str = staff_names[0] + ',' + staff_names[1] + '(+' + (staff_names.length - 2) + 'more)';
          }
          else
          {
            staff_str = staff_names.join(',');
          }
          schedules[i].staff_str = staff_str;
        }
        this.setState({ schedules, staffs });
      }
      else
      {
        this.showMessage(res1.data.message);
      }
    }).catch(e => {
      console.log(e);
      this.showMessage('Failed to get data');
    })
  }

  onDayChanged = (e) => {
    this.setState({ date: e.target.value });
    this.refreshData(e.target.value);
  }

  onCloseClick = () => {
    this.props.history.goBack();
  }

  
  render()
  {
    return (
      <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Form>
                  <Form.Row style={{ alignItems: 'center' }}>
                    <Card.Title as="h4" className='mr-2 mb-3'>Class Schedule on</Card.Title>
                    <Form.Group>
                      <Form.Control
                        type="date"
                        value={this.state.date}
                        onChange={this.onDayChanged}
                      ></Form.Control>
                    </Form.Group>
                  </Form.Row>
                </Form>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                <Table className="table-hover table-striped">
                  <thead>
                    <tr>
                      <th className="border-0"></th>
                      <th className="border-0">Class time</th>
                      <th className="border-0"># of students</th>
                      <th className="border-0">Instructors</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      this.state.schedules.map(sc => (
                        <tr key={sc._id}>
                          <td><a href={'/admin/class/edit?id='+sc.class_id}>{sc.class_name}</a></td>
                          <td>{ sc.time_start } - { sc.time_end }</td>
                          <td>{ sc.num_students }</td>
                          <td>{ sc.staff_str }</td>
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
          <Row>
            <Col md="12">
              <Button onClick={this.onCloseClick} className="float-right mr-1" variant="outline-secondary">Close this view</Button>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}
export default DailyClass;
